.skillCard {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
  width: 30%;
  border-radius: 10px;
  border: 2px solid #000;
  width: 400px;
  height: 215px;
  margin: 24px;

  h2 {
    font-size: 20px;
    font-weight: 700;
  }
  p {
    font-size: 16px;
    width: 346px;
    text-align: center;
  }
  @media (max-width: 450px) {
    width: 300px;
    height: 250px;
    p {
      padding: 10px;
      width: 100%;
    }
  }
}
