.learnMore {
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding-bottom: 10%;
  padding-top: 125px;
  * {
    width: 100%;
  }
  .back a {
    text-align: left;
    cursor: pointer;
  }

  .line {
    border: none;
    border-bottom: 2px solid #000;
    width: 80px;
  }
  h2 {
    font-size: 20px;
    text-align: left;
    padding-bottom: 17px;
  }
  ul {
    padding-left: 15px;
  }
  #title {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 17px;
    h1 {
      font-size: 24px;
      padding-bottom: 28px;
      width: 100%;
      text-align: center;
    }
    .line {
      width: 50%;
    }
  }
  p {
    display: flex;
    flex-direction: column;
    gap: 17px;
    a {
      display: flex;
      width: 100%;
      justify-content: center;
      text-decoration: none;
      button {
        height: 42px;
        width: auto;
        padding-left: 41px;
        padding-right: 41px;
        margin: 0;
        border: 0;
        background-color: #ecd6fc;
        border-radius: 10px;
        text-decoration: none;
        align-items: center;
        flex-shrink: 0;
        flex-grow: 0;
        &:hover {
          background-color: #d4b5f9;
        }
      }
    }
  }
}
@media (max-width: 880px) {
  .learnMore {
    padding: 50px 0;
    p,
    h2 {
      text-align: center;
    }

    ul {
      padding-left: 0;
      list-style-position: inside;
      text-align: center;
    }

    li {
      display: list-item; // ensures normal bullet behavior
    }
    .subtitle {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }
}
